.Canvas {
  width: 100%;
  height: 100%;
  position: relative;
  background: -webkit-gradient(linear, left bottom, right top, from(rgb(0, 22, 61)), to(rgb(0, 12, 34)));
  background: -webkit-linear-gradient(bottom left, rgb(0, 22, 61), rgb(0, 12, 34));
  background: -o-linear-gradient(bottom left, rgb(0, 22, 61), rgb(0, 12, 34));
  background: linear-gradient(to top right, rgb(0, 22, 61), rgb(0, 12, 34));
}

.Canvas-canvas {
  position: absolute;
  width: 100%;
  height: 100%;
}



